.project-1-preview {
    background-image: url(../../images/project-1-1.png);
    width: 400px;
    height: 240px;
    transition-duration: 0.3s;
    border: 1px solid white;
}

.project-1-preview:hover {
    background-image: url(../../images/project-1-2.png);
    opacity: 0.7;
}

