.project-container {
    width: 40%;
    border: 2px solid white;
    margin: 4% 2%;
    border-radius: 15px;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
}

.project-container-wrapper {
    display: flex;
}

.project-description {
    margin: 3% 5%;
    line-height: 1.6em;
}

.project-skills-container {
    display: flex;
}

.project-skills-container p {
    border: 1px solid #2f2f2f;
    padding: 2px;
    margin: 5px;
    background: white;
    color: black;
    font-weight: bold;
    box-shadow: -5px 6px 5px 0px rgba(0,0,0,0.75);
}