.project-container {
    width: 40%;
    border: 2px solid white;
    margin: 4% 2%;
    border-radius: 15px;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
}

.project-container-wrapper {
    display: flex;
}

.project-description {
    margin: 3% 5%;
    line-height: 1.6em;
}

.project-skills-container {
    display: flex;
}

.project-skills-container p {
    border: 1px solid #2f2f2f;
    padding: 2px;
    margin: 5px;
    background: white;
    color: black;
    font-weight: bold;
    box-shadow: -5px 6px 5px 0px rgba(0,0,0,0.75);
}
.icon-copyright {
    color: white;
    font-size: 10px;
    margin-top: 30%;
    text-align: center;
}

.icon-copyright a {
    text-decoration: none;
    color: gray;
}

.social-media-container {
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 50px;
}

.social-media-link {
    height: 100px;
    margin: 30px;
}

.email-container {
    display: flex;
    flex-direction: column;
    color: white;
    align-items: center;
}

.social-media-icon {
    height: 50px;
    -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s;
}

.social-media-icon:hover {
    opacity: 0.5;
}

.social-media-wraper {
    margin: 50px;
}
body {
    font-family: 'Roboto', sans-serif;
    background: #2f2f2f;
}

.header-container {
    padding: 10px 0;
    color: white;
    background: #2f2f2f;
    text-align: center;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 5px solid white;
}

.intro-cursor {
    color: white;
}

.intro-text {
    color: white;
    font-size: 36px;
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 15% 0;
    line-height: 1.8em;
    font-family: 'Roboto Mono', monospace;
}

.intro-text p {
    margin: 0;
}

.logo {
    width: 100px;
    margin: 0 15px;
}

.nav-link {
    text-decoration: none;
    color: white;
    padding: 10px;
    font-size: 18px;
    font-weight: bold;
    display: inline-block;
    position: relative;
}

.nav-link::after {
    position: absolute;
    top: 40px;
    background: #2f2f2f;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    left:calc(50% - 4px);
    -webkit-transition-duration: 2s;
            transition-duration: 2s;
}

.nav-link:hover::after {
    content: "";
    position: absolute;
    top: 40px;
    background: white;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    left:calc(50% - 4px);
}

.nav-link-selected {
    text-decoration: none;
    color: white;
    padding: 10px;
    font-size: 18px;
    font-weight: bold;
    display: inline-block;
    position: relative;
}

.nav-link-selected:after {
    content: "";
    position: absolute;
    top: 40px;
    background: white;
    width: 30px;
    height: 5px;
    border-radius: 15%;
    left:calc(50% - 13px);
}

.nav-link-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    list-style: none;
    width: 500px;
    margin: auto;
}

.nav-link-container li {
    text-decoration: none;
}
